* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --index: calc(1vw1vh);
  --text: #f6f6df;
}

@font-face {
  font-family: raleway_f;
  src: url("raleway-regular.d9b4f7c4.woff2");
}

@font-face {
  font-family: raleway_f;
  src: url("raleway-black.5e85eee1.woff2");
  font-weight: 900;
}

body {
  background-color: #010101;
  font-family: raleway_f, sans-serif;
  line-height: 1.55;
}

.main-header {
  position: relative;
}

.main-header:after {
  content: "";
  z-index: 100;
  height: calc(var(--index) * 10);
  bottom: calc(var(--index) * -3.5);
  background-image: url("ground.97d7f054.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  position: absolute;
}

.layers {
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

.layer {
  will-change: transform;
  z-index: 2;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}

.layers__base {
  transform: translate3d(0, calc(var(--scrollTop) / 1.6), 0);
  z-index: 0;
}

.layers__middle {
  transform: translate3d(0, calc(var(--scrollTop) / 2.5), 0);
}

.layers__front {
  transform: translate3d(0, calc(var(--scrollTop) / 6), 0);
}

.layer__header {
  z-index: 1;
  transform: translate3d(0, calc(var(--scrollTop) / 2), 0);
  will-change: transform;
  text-transform: uppercase;
  color: var(--text);
  text-shadow: 0 0 15px #efd498;
  text-transform: uppercase;
  font-weight: 900;
  font-size: calc(var(--index) * 1.8);
  letter-spacing: calc(var(--index) / 7.5);
}

.layers__title {
  font-size: calc(var(--index) * 2.5);
  letter-spacing: calc(var(--index) / 3);
}

.layers__caption {
  font-size: calc(var(--index) / 1);
  letter-spacing: calc(var(--index) / 4);
  margin-top: calc(var(--index) * 1);
}

.main-article {
  color: var(--text);
  text-align: center;
  background-position: center;
  background-size: cover;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
  position: relative;
}

.main-article__header {
  text-transform: uppercase;
  font-size: calc(var(--index) * 1.8);
  letter-spacing: calc(var(--index) / 7.5);
  transform: translate3d(0, calc(var(--scrollTop) / -7.5), 0);
  will-change: transform;
}

.main-article_facts-cat {
  max-width: calc(var(--index) * 30);
  font-size: calc(var(--index) * 1);
  margin-top: calc(var(--index) / 1);
  transform: translate3d(0, calc(var(--scrollTop) / -7.5), 0);
  will-change: transform;
}

.copy {
  bottom: calc(var(--index) * 2.5);
  opacity: .45;
  font-size: calc(var(--index) * 1);
  letter-spacing: calc(var(--index) / 37.5);
  position: absolute;
}

